button {
    color: var(--text-black);
    font-size: 20px;
    font-weight: 400;

    outline: none;
    border: none;

    overflow: hidden;
    user-select: none;
    cursor: pointer;
}


/* Отключена */
button:disabled {
    cursor: not-allowed;
}


/* Кнопки в навигации */
.button-nav {
    height: 47px;
    padding: 0 28px;

    display: flex;
    justify-content: center;
    align-items: center;

    border: 1px solid transparent;
    border-radius: 24px;
    background-color: transparent;

    font-size: 20px
}

.button-black {
    height: 64px;
    padding: 0 120px;
    
    border-radius: 40px;
    background-color: transparent;
    border: 1px solid black;

    color: var(--text-black);
    text-transform: uppercase;
    letter-spacing: 1.4px;
}

.button-brief {
    height: 64px;
    padding: 0 80px;

    border-radius: 24px;
    background: rgb(243, 243, 243);
    
    letter-spacing: 1.6px;
}

.button-brief_thin {
    height: 55px;

    margin-top: 40px;
}

.button-brief.active {
    background-color: #858484;
    color: #ffffff;
}


/* Только для десктопных устройств */
@media (hover: hover) {
    .button-nav:hover {
        border: 1px solid var(--border-black);
    }

    .button-black:hover {
        color: var(--text-white);
        background-color: black;
    }

    .button-brief:hover {
        color: var(--text-white);
        background-color: #858484;
    }
}

/* Только на мобильных девайсах */
@media (hover: none) {
    .button-nav:active {
        border: 1px solid var(--border-black);
    }

    .button-black:active {
        color: var(--text-white);
        background-color: black;
    }

    .button-brief:active {
        color: var(--text-white);
        background-color: #858484;
    }
}