.brief-article {
    gap: 120px;
    padding: 80px 0;
}

.brief__title_big {
    gap: 70px;
}

.brief__title_big h1 {
    font-weight: 500;
    line-height: 46px;
}

.brief__title_big h2 a {
    border-bottom: 2px solid var(--border-black);
}

.brief__title {
    margin-bottom: 30px;
}

.brief__buttons-container {
    flex-wrap: wrap;
    gap: 20px;
}

.brief-tasks {
    width: 100%;
    height: 40px;
    padding-bottom: 20px;
    min-height: 40px;
    max-height: 400px;

    color: black;
    font-size: 20px;

    outline: none;
    border: none;
    border-bottom: 1px solid black;

    resize: none;

    transition: none;
}

.brief-tasks::placeholder {
    color: rgb(217, 217, 217);
    font-size: 20px;
}

.brief-tasks::-webkit-scrollbar {
    display: none;
}


/* Форма */
.brief__form {
    flex-wrap: wrap;
    gap: 60px;
}

.brief__form input {
    width: 540px;
}

.brief__form-button {
    width: 251px;
    height: 251px;

    border-radius: 50%;
    background-color: var(--bg-blue);

    color: var(--text-white);
    font-size: 20px;
    letter-spacing: 1.7px;

    margin: 120px auto 0 auto;
}